import React, { Component } from 'react';
import GoogleMapReact from 'google-map-react';

const AnyReactComponent = ({ text }) => <div>{text}</div>;

class Restlist extends Component {

    static defaultProps = {
        center: {
          lat: 30.2672,
          lng: -97.7431
        },
        zoom: 15
      };
     
      render() {
        return (
          <div class="form-group">
          <select multiple="5" class="form-control" id="exampleSelect2" style={{height: 600}}>
            <option>1</option>
            <option>2</option>
            <option>3</option>
            <option>4</option>
            <option>5</option>
          </select>
        </div>
        );
      }
    }
     
    export default Restlist;